.s-video-rooms {
	background: $themeColor2;
	padding: 110px 0;
	@include laptop {
		padding: 80px 0; }
	@include tablet {
		padding: 72px 0; }
	@include md {
		padding: 103px 0; }
	@include sm {
		padding: 40px 0; } }

.video-rooms {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin: 62px 0 30px;
	padding: 30px 0;

	@include md {
		margin: 30px 0 20px; }
	@include sm {
		margin: 10px 0 10px; }

	&__item {
		position: relative;
		margin: 10px 20px;
		padding: 32px 32px 22px;
		background: transparent;
		border: 1px solid #2c302d;
		width: 300px;
		display: flex;
		flex-direction: column;
		align-items: center;
		transition: all ease 0.1s;
		border-radius: 10px;
		border: 4px solid #3d464e;

		@include md {
			margin: 10px 10px; }

		@include sm {
			margin: 10px 0; }

		&.hoverable {

			&:hover {
				background: $mainBg;

				.video-rooms__btn {
					border-color: $themeColor;

					&:hover {
						background: $themeColor;
						color: #333; } } } } }

	&__link {
		position: absolute;
		display: block;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0; }

	&__img-wrap {
		display: flex;
		width: 100%;
		height: 168px;
		justify-content: center; }

	&__img {
		display: block;
		margin:  auto;
		max-height: 100%; }

	&__num {
		display: none; }

	&__title {
		display: none; }

	&__users {
		margin: 34px auto 26px; }

	&__user {
		display: flex;
		align-items: center;
		flex: 1 1 auto;
		margin: 20px auto 0;

		&:first-child {
			margin: 0; } }

	&__avatar {
		width: 48px;
		height: 48px;
		margin: 0 18px 0 0;
		border-radius: 50%;
		object-fit: cover; }

	&__name {
		font-size: 16px;
		line-height: 1.28; }

	&__btn {
		margin: auto 0 0;
		display: flex;
		align-items: center;
		justify-content: center;
		min-height: 54px;
		padding: 0 20px;
		font-size: 12px;
		color: $themeColor;
		text-transform: uppercase;
		text-decoration: none;
		border: 2px solid transparent;
		text-align: center;
		transition: all ease 0.1s;
		cursor: pointer;
		transition: all ease .1s;
		border-radius: 5px; } }
