.btn {
	display: flex;
	text-align: center;
	background-color: $themeColor;
	border: 2px solid $themeColor;
	padding: 26px 30px;
	align-items: center;
	justify-content: center;
	font-size: 14px;
	cursor: pointer;
	@include medium;
	color: $white;
	text-decoration: none;
	transition: all ease 0.3s;
	transform: translateZ(0) perspective(1px);
	backface-visibility: hidden;
	//transform-style: preserve-3d
	-webkit-font-smoothing: subpixel-antialiased;
	-webkit-backface-visibility: hidden;
	//-moz-backface-visibility: hidden
	//-ms-backface-visibility: hidden
	//-webkit-transform-style: preserve-3d
	border-radius: 5px;

	@include tablet {
		padding: 25px 20px; }

	&:hover {
		border-color: $white; }

	&--gradient {
		border: 2px solid transparent;
		color: #000;
		font-size: 14px;

		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			right: 0;
			z-index: -2;
			background: linear-gradient(145deg, #fef502, #fef502);
			animation: AnimationName 5s linear infinite; }

		&:before {
			transition: 0.2s;
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			right: 0;
			z-index: -1;
			background: linear-gradient(145deg, #fef502, #fef502);
			animation: AnimationName 5s linear infinite; }

		&:hover {
			&:before {
				opacity: 0; } }
		@include tablet {
			padding: 19px 23px; } }

	&--transparent {
		border: 2px solid transparent;
		background: transparent;
		color: $themeColor;

		&:hover {
			background: #161917;
			border-color: $themeColor;
			color: $white; } }

	&--border {
		border: 2px solid transparent;
		color: #FFF;
		font-size: 14px;

		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			right: 0;
			z-index: -2;
			background: linear-gradient(145deg, #fef502, #fef502);
			animation: AnimationName 5s linear infinite; }

		&:before {
			transition: 0.2s;
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			right: 0;
			z-index: -1;
			background: #161917;
			animation: AnimationName 5s linear infinite; }

		&:hover {
			background: $themeColor;
			color: #000;
			&:before {
				opacity: 0; } }
		// +tablet
 }		//adding: 19px 23px

	&--border-transparent {
		font-size: 12px;
		background: transparent;
		border: 2px solid $accent;
		color: $accent;
		padding: 15px 20px;
		min-height: 50px;

		@include tablet {
			padding: 15px 20px; }

		&:hover {
			background: $accent;
			color: $black;

			.icon {
				fill: $black; } } }

	&--alt {
		border: 2px solid transparent;
		color: $black;
		padding: 15px 20px;
		min-height: 50px;
		background: $themeColorD;

		@include tablet {
			padding: 15px 20px; }

		&:hover {
			border-color: $white;
			color: $black;

			.icon {
				fill: $black; } } } }




.icon-link {
	color: #fff;
	.icon {
		font-size: 18px;
		fill: $white; } }

.theme-link {
	font-size: 14px;
	color: $themeColor;
	text-transform: uppercase;
	text-decoration: none;
	&:hover {
		text-decoration: underline; } }

